// Assets
import right from '../../../assets/icons/arrow-right.svg'
import left from '../../../assets/icons/arrow-left.svg'

export default class extends React.Component {
    render() {
	    return (
	        <div> 
	        	<img src={right} alt="Página siguiente" onClick={this.handleClick} /> 	
	        	<img src={left} alt="Página anterior" onClick={this.handleClick} />	        	   
	        	<style jsx>{`
	        		img{
	        			float:right;
	        			padding-left:12px;
	        		}
	        	`}</style>      
	        </div>
	    );
	}
}


